/*global kakao */
//        
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { actionCreators as locateActions } from "../redux/modules/loc";
import { Grid } from "../elements";

import { customAlert } from "./Sweet";
import logger from "../shared/Console";

const MapContainer = (props) => {
  const dispatch = useDispatch();

  const infoRef = React.useRef("");
  //  -   , 
  const latitude = useSelector((state) => state.user.user.latitude);
  const longitude = useSelector((state) => state.user.user.longitude);

  React.useEffect(() => {
    //   infowindow
    let infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });

    //   container (JSX - <Grid id="map">)
    //  (center)  (level)(      )
    const container = document.getElementById("map");
    const options = {
      center: new kakao.maps.LatLng(
        latitude ? latitude : 33.450701,
        longitude ? longitude : 126.570667
      ),
      level: 3,
    };

    //  
    const map = new kakao.maps.Map(container, options);
    //       
    const ps = new kakao.maps.services.Places();
    ps.keywordSearch(props?.searchPlace, placesSearchCB);

    // input      
    function placesSearchCB(data, status, pagination) {
      if (data.length === 0) {
        return customAlert.sweetOK(
          "   ",
          " +  .",
          "     ."
        );
      }
      if (status === kakao.maps.services.Status.OK) {
        let bounds = new kakao.maps.LatLngBounds();

        //   (OK) displayMarker  
        for (let i = 0; i < data.length; i++) {
          displayMarker(data[i]);
          bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x));
        }
        //      ,    ,      
        map.setBounds(bounds);
      }
      if (status === kakao.maps.services.Status.ERROR) {
        return customAlert.sweetOK(
          " ",
          "   .",
          "   ."
        );
      }
    }

    //    place   
    function displayMarker(place) {
      let marker = new kakao.maps.Marker({
        map: map,
        position: new kakao.maps.LatLng(place.y, place.x),
      });

      //    ,  
      infowindow.setContent(
        '<div style="padding:0.5rem;font-size:1rem;width:max-content;">' +
          place.place_name +
          "</div>"
      );
      infowindow.open(map, marker);

      //    ,    
      kakao.maps.event.addListener(marker, "mouseover", function () {
        infowindow.setContent(
          '<div style="padding:0.5rem;font-size:1rem;width:max-content;">' +
            place.place_name +
            "</div>"
        );
        infowindow.open(map, marker);
      });

      //     ,    
      kakao.maps.event.addListener(marker, "mouseout", function () {
        infowindow.close();
      });
      kakao.maps.event.addListener(map, "click", function () {
        infowindow.close();
      });

      //      ,        modal 
      kakao.maps.event.addListener(marker, "click", function () {
        customAlert
          .sweetPromise(
            "  ?",
            place.place_name,
            place.road_address_name
          )
          .then((res) => {
            if (res === true) {
              dispatch(locateActions.setShopAddress(place.place_name));
              dispatch(locateActions.setPlaceUrl(place.place_url));
              props?.close();
              props?.placeNull();
            }
            return;
          });
      });
    }
  }, [props?.searchPlace]);

  return (
    <Grid className="map_wrap">
      <Grid
        id="map"
        absolute="relative"
        overflow="hidden"
        width="auto"
        height="100vh"
      />
    </Grid>
  );
};
export default MapContainer;
